import pawHideIcon from "@/assets/pawHide.png";
import pawShowIcon from "@/assets/pawShow.png";
import chuangkouguanbi from "@/assets/chuangkouguanbi.png";
export default {
  data() {
    return {
      pawIcon: pawHideIcon,
      ueserPaw: "",
      pwdType: "password",
      close: chuangkouguanbi,
      errmsg: ""
      // isSecurity:false
    };
  },
  props: {
    isSecurity: { type: Boolean }
  },
  methods: {
    //密码显示or加密
    changeType() {
      if (this.pwdType === "password") {
        this.pwdType = "text";
        this.pawIcon = pawShowIcon;
      } else {
        this.pwdType = "password";
        this.pawIcon = pawHideIcon;
      }
    },
    // closeFun(){this.$emit('closeSecurity');}
    closeFun() {
      this.ueserPaw = "";
      this.$emit("closeFun");
    },
    getPassword() {
      var that = this,
        formData = {
          sign: that.$store.state.user.sign,
          access_token: that.$store.state.user.token,
          password: that.ueserPaw
        };
      if (that.ueserPaw == "") {
        that.errmsg = "登录密码不能为空";
      } else {
        that
          .$request({
            method: "POST",
            url: that.$baseURL + "/personal/check-password",
            data: that.$qs.stringify(formData)
          })
          .then(function(response) {
            if (response.code == 0) {
              that.$emit("getSubmitSecurity");
              that.errmsg = response.errmsg;
              that.$router.push("/wallet");//钱包
            } else {
              that.errmsg = response.errmsg;
            }
          })
          .catch(function(error) {
            console.log(error);
          });
      }
    },
    // 忘记密码
    getForgot() {
      this.$router.push("/retrievePaw");
    }
  }
};



